'use strict';


module.exports = function(parentModule) {

    parentModule._module
        .factory('List', function($resource) {
            return $resource('/lists/:id'); // Note the full endpoint address
        });

    parentModule._module
        .factory('Message', function($resource) {
        return $resource('/message/:id'); // Note the full endpoint address
    });


};



