'use strict';


module.exports = function(parentModule) {



    parentModule._module.controller('Flare.ListPageController', ['$scope', '$http', 'List', function($scope, $http, List) {
        let vm = this;
        let title = "List Admin";
        let newlist = new List;

        var lists = List.query(function() {
            console.log(lists);
        }); //query() returns all the entries

        //console.log('hello');

        angular.extend(this, {
            title: title,
            lists: lists
        });
    }]);

    parentModule.CreateController = parentModule._module.controller('Flare.CreateController', ['$scope', '$http', 'Message', 'List', function($scope, $http, Message, List) {
        let vm = this;
        let test = "Send Flare";
        let message = "";

        let newmsg = new Message;


        let saveflare = function(){
            console.log(newmsg);
            newmsg.listname = "test";
            Message.save(newmsg);
        };

        var lists = List.query(function() {
            console.log(lists);
        }); //query() returns all the entries



        angular.extend(this, {
            test: test,
            message: message,
            saveflare: saveflare,
            newmsg: newmsg,
            lists: lists
        });

    }]);

};



